import "./styles.css";
import kou from "../src/kou";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1
  },
  paper: {
    height: 140,
    width: 100
  },
  control: {
    padding: theme.spacing(2)
  }
}));

function menus(index) {
  const items = [];
  const keys = Object.keys(kou[index]);
  const values = Object.values(kou[index]);
  for (let i = 0; i < values.length; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}

function all_menus() {
  const items = [];
  for (let i = 0; i < kou.length; i++) {
    items.push(menus(i));
  }
  return <>{items}</>;
}
export default function App() {
  return <>{all_menus()}</>;
}
